#label-score {
    font-size: 2.5rem;
    font-weight: 600;
}

#container-gamescore{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    padding-top: 0.5rem;
}

#label-result {
    font-size: 5rem; 
    font-weight: 600;
}
@keyframes shake{
    0% {
        transform: translate(-50%, -50%)
    }
    25% {
        transform: translate(-50%, -49%) rotate(4deg)
    }
    50% {
        transform: translate(-50%, -50%) 
    }   
    75% {
        transform: translate(-50%, -49%) rotate(-4deg)
    }
    100% {
        transform: translate(-50%, -50%) 
    }
}

@keyframes arise {
    from {transform:translate(-50%, -50%) scale(0.1); opacity: 0;}
    to {transform:translate(-50%, -50%); opacity: 1; }
}

#container-gameend {
    position:absolute;
    display : flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    gap: 0.5rem;
    animation: arise 0.3s;
    animation-timing-function: linear;
}
#label-resultscoretext{
    font-size : 0.7rem;
    color: black;
}

#button-gobacktopreview {
    --background-color: rgb(255, 187, 51);
    --background-color-hover: rgb(255, 204, 102);
    color: white;
    font-family: inherit;
    font-size: 1rem;
    letter-spacing: 0.1rem;
    font-weight: 600;
    text-align: center;
    border-radius: 0.3rem;
    border-width: 0;
    outline-width: 5px;
    padding : 0.5rem;
    width: 8rem;
    margin: 0.5rem;
  
}

#container-oprankinfo{
    background-color: rgba(0,0,0,0.3);
    display: flex;
    position: absolute;
    left: 100%;
    padding: 0.5rem;
    transform: translateX(-100%);
    border-width: 10px;
    border-bottom-left-radius: 0.5rem;
    gap: 0.2rem;
    padding-right: 1rem;
}
#container-oprankinfo > label {
    font-size: 2.3rem;
    font-weight: 900;
}
#container-oprankinfo > article {
    font-weight: 700;
    font-size: 0.7rem;
    color: black;
}
#container-oprankinfo > article > text {
    font-size: 0.5rem;
    font-weight: 300;
    color: white;
}


#container-myrankinfo{
    background-color: rgba(0,0,0,0.3);
    display: flex;
    position: absolute;
    left:0%;
    padding: 0.5rem;
    border-width: 10px;
    border-bottom-right-radius: 0.5rem;
    gap: 0.2rem;
    padding-left: 1rem;
}
#container-myrankinfo > label {
    font-size: 2.3rem;
    font-weight: 900;
}
#container-myrankinfo > article {
    font-weight: 700;
    font-size: 0.7rem;
    color: black;
}
#container-myrankinfo > article > text {
    font-size: 0.5rem;
    font-weight: 300;
    color: white;
}
